import { ComponentModule } from '@/main';
import { TOAST_TRIGGER } from '@/models/events';
import { ToastLevel } from '@/models/toast';

export default {
    name: () => 'toast',
    template: () => import('./toast.html?raw'),
    data: () => ({
        level: null as ToastLevel | null,
        show: false,
        message: '',
        timeout: 3000,
        init() {
            console.log('toast@init');

            this.$nextTick(() => {
                // @ts-expect-error 2769
                window.addEventListener(TOAST_TRIGGER, (e: CustomEvent) => {
                    console.log('toast@event', e.detail);

                    this.show = true;
                    this.level = e.detail?.level || null;
                    this.message = e.detail?.msg || '';
                    setTimeout(() => {
                        this.show = false;
                        this.level = null;
                        this.message = '';
                    }, this.timeout);
                });
            });
        },
    }),
} as ComponentModule;
